@import '../_var'

.footer
    width: 100%
    padding: 6rem 0
    background-color: rgba(19, 19, 19, .8)
    .footer-container
        max-width: 1140px
        margin: auto
        display: grid
        grid-template-columns: repeat(2,1fr)
        grid-gap: 40px
        .left
            height: 100%
            max-width: 100%
            display: flex
            flex-direction: column
            padding: 1rem
            .adress
                display: flex
                margin-bottom: .8rem
                p
                    line-height: 30px
        .right
            height: 100%
            max-width: 100%
            display: flex
            flex-direction: column
            padding: 1rem
            .social
                margin-top: 1rem
h4
    font-size: 1.2rem
    padding-bottom: 0.7rem

p
    font-size: 1.2rem

@include md
    .footer
        .footer-container
            grid-template-columns: 1fr
            p
                font-size: 1rem
